@import "https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;600&display=swap";
:root {
  --primary-color: #4caf50;
  --secondary-color: #45a049;
  --background-color: #f0f2f5;
  --card-background: #fff;
  --text-color: #333;
  --light-text-color: #666;
  --white-color: #fff;
  --border-color: #e0e0e0;
  --shadow-light: #0000001a;
  --shadow-dark: #0003;
  --invalid-color: #ff4d4d;
  --valid-color: #4caf50;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: var(--background-color);
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  font-family: Poppins, sans-serif;
  display: flex;
}

#auth-page-container {
  justify-content: center;
  align-items: center;
  width: 100%;
  padding: 1rem;
  display: flex;
}

.app-logo {
  width: 80px;
  height: 80px;
  margin-bottom: 1rem;
}

.mobile-logo-container {
  text-align: center;
  width: 100%;
  padding: 1rem 0;
  display: none;
}

.mobile-app-logo {
  width: 60px;
  height: 60px;
}

.auth-container-animated {
  background-color: var(--card-background);
  box-shadow: 0 5px 15px var(--shadow-dark);
  border-radius: 30px;
  width: 100%;
  max-width: 768px;
  min-height: 480px;
  position: relative;
  overflow: hidden;
}

.auth-container-animated p {
  letter-spacing: .3px;
  margin: 20px 0;
  font-size: 14px;
  line-height: 20px;
}

.auth-container-animated span {
  color: var(--light-text-color);
  margin-bottom: 10px;
  font-size: 12px;
}

.auth-container-animated a {
  color: var(--text-color);
  margin: 15px 0 10px;
  font-size: 13px;
  text-decoration: none;
}

.auth-container-animated button {
  background-color: var(--primary-color);
  color: var(--white-color);
  letter-spacing: .5px;
  text-transform: uppercase;
  cursor: pointer;
  border: 1px solid #0000;
  border-radius: 8px;
  margin-top: 10px;
  padding: 10px 45px;
  font-size: 12px;
  font-weight: 600;
  transition: all .2s ease-in-out;
}

.auth-container-animated button.hidden {
  border-color: var(--white-color);
  background-color: #0000;
}

.auth-container-animated button:hover {
  box-shadow: 0 3px 8px var(--shadow-light);
  transform: scale(1.05);
}

.auth-container-animated form {
  background-color: var(--card-background);
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  padding: 0 40px;
  display: flex;
}

.auth-container-animated input {
  background-color: #eee;
  border: none;
  border-radius: 8px;
  outline: none;
  width: 100%;
  margin: 8px 0;
  padding: 10px 15px;
  font-size: 13px;
}

.form-container {
  height: 100%;
  transition: all .6s ease-in-out;
  position: absolute;
  top: 0;
}

.sign-in {
  z-index: 2;
  width: 50%;
  left: 0;
}

.sign-up {
  opacity: 0;
  z-index: 1;
  width: 50%;
  left: 0;
}

.auth-container-animated.active .sign-in {
  transform: translateX(100%);
}

.auth-container-animated.active .sign-up {
  opacity: 1;
  z-index: 5;
  animation: .6s move;
  transform: translateX(100%);
}

@keyframes move {
  0%, 49.99% {
    opacity: 0;
    z-index: 1;
  }

  50%, 100% {
    opacity: 1;
    z-index: 5;
  }
}

.social-icons {
  margin: 20px 0;
}

.social-icons a {
  border: 1px solid var(--border-color);
  border-radius: 20%;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  margin: 0 3px;
  transition: all .2s ease-in-out;
  display: inline-flex;
}

.social-icons a:hover {
  border-color: var(--primary-color);
  transform: scale(1.1);
}

.toggle-container {
  z-index: 1000;
  border-radius: 150px 0 0 150px;
  width: 50%;
  height: 100%;
  transition: all .6s ease-in-out;
  position: absolute;
  top: 0;
  left: 50%;
  overflow: hidden;
}

.auth-container-animated.active .toggle-container {
  border-radius: 0 150px 150px 0;
  transform: translateX(-100%);
}

.toggle {
  background-color: var(--primary-color);
  background: linear-gradient(to right, var(--secondary-color), var(--primary-color));
  height: 100%;
  color: var(--white-color);
  width: 200%;
  height: 100%;
  transition: all .6s ease-in-out;
  position: relative;
  left: -100%;
  transform: translateX(0);
}

.auth-container-animated.active .toggle {
  transform: translateX(50%);
}

.toggle-panel {
  text-align: center;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 50%;
  height: 100%;
  padding: 0 30px;
  transition: all .6s ease-in-out;
  display: flex;
  position: absolute;
  top: 0;
  transform: translateX(0);
}

.toggle-left {
  transform: translateX(-200%);
}

.auth-container-animated.active .toggle-left {
  transform: translateX(0);
}

.toggle-right {
  right: 0;
  transform: translateX(0);
}

.auth-container-animated.active .toggle-right {
  transform: translateX(200%);
}

@media (width <= 768px) {
  .app-logo {
    display: none;
  }

  .mobile-logo-container {
    display: block;
  }

  body {
    flex-direction: column;
  }

  #auth-page-container {
    height: 100vh;
    padding: 0;
  }

  .auth-container-animated {
    max-width: 100%;
    min-height: 100vh;
    box-shadow: none;
    border-radius: 0;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    display: flex;
  }

  .toggle-container {
    display: none;
  }

  .form-container {
    width: 100%;
    height: auto;
    box-shadow: none;
    padding: 20px;
    position: static;
    opacity: 1 !important;
    animation: none !important;
    transform: none !important;
  }

  .sign-in, .sign-up {
    z-index: 1;
    width: 100%;
  }

  .auth-container-animated.active .sign-in, .auth-container-animated:not(.active) .sign-up {
    display: none;
  }

  .auth-container-animated form {
    padding: 20px;
  }

  .mobile-toggle-form {
    margin-top: 15px;
    display: block;
  }
}

.color-overlay {
  background-color: var(--primary-color);
  z-index: 9999;
  pointer-events: none;
  border-radius: 50%;
  width: 0;
  height: 0;
  transition: width .6s ease-out, height .6s ease-out, background-color .3s;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.color-overlay.active {
  width: 200vmax;
  height: 200vmax;
}

.color-overlay.login-color {
  background-color: var(--primary-color);
}

.color-overlay.signup-color {
  background-color: var(--secondary-color);
}

.mobile-toggle-form {
  display: none;
}

@media (width <= 768px) {
  .mobile-toggle-form {
    display: block;
  }
}

.password-strength-meter {
  background-color: #e0e0e0;
  border-radius: 4px;
  width: 100%;
  height: 8px;
  margin: 10px 0;
  overflow: hidden;
}

.strength-bar {
  background-color: var(--invalid-color);
  border-radius: 4px;
  justify-content: center;
  align-items: center;
  width: 0%;
  height: 100%;
  transition: width .3s ease-in-out, background-color .3s ease-in-out;
  display: flex;
}

#strength-text {
  color: #fff;
  white-space: nowrap;
  font-size: 10px;
  font-weight: 600;
}

.strength-bar.weak {
  background-color: var(--invalid-color);
}

.strength-bar.medium {
  background-color: orange;
}

.strength-bar.strong {
  background-color: var(--valid-color);
}
